import React from 'react';

import './Chart.css'
import {
    
    LineChart,
    Line,
    XAxis,
    YAxis,
    CartesianGrid,
    Legend,
    Tooltip
  } from "recharts";

const data = [
    {
      "name": "Jan",
      "uv": 4000,
      "pv": 2400,
      "amt": 2400
    },
    {
      "name": "Feb",
      "uv": 3000,
      "pv": 1398,
      "amt": 2210
    },
    {
      "name": "March",
      "uv": 2000,
      "pv": 9800,
      "amt": 2290
    },
    {
      "name": "April",
      "uv": 2780,
      "pv": 3908,
      "amt": 2000
    },
    {
      "name": "May",
      "uv": 1890,
      "pv": 4800,
      "amt": 2181
    },
    {
      "name": "June",
      "uv": 2390,
      "pv": 3800,
      "amt": 2500
    },
    {
      "name": "July",
      "uv": 3490,
      "pv": 4300,
      "amt": 2100
    },
    {
      "name": "August",
      "uv": 3490,
      "pv": 4300,
      "amt": 2100
    },
    {
      "name": "Sep",
      "uv": 3490,
      "pv": 4300,
      "amt": 2100
    },
    {
      "name": "October",
      "uv": 3490,
      "pv": 4300,
      "amt": 2100
    },
    {
      "name": "Nov",
      "uv": 3490,
      "pv": 4300,
      "amt": 2100
    },
    {
      "name": "Dec",
      "uv": 3490,
      "pv": 4300,
      "amt": 2100
    }

  ]
 function Chart() {
    return (
        <div className='chart'>
           <h3 className='heading'> Sales Analytics</h3>
           <LineChart width={730} height={250} data={data}
  margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
  <CartesianGrid strokeDasharray="3 3" />
  <XAxis dataKey="name" />
  <YAxis />
  <Tooltip />
 <Legend/>
  <Line type="monotone" dataKey="pv" stroke="#8884d8" />
  <Line type="monotone" dataKey="uv" stroke="#82ca9d" />
</LineChart>
        </div>
    )
}
export default Chart;